.gameContainer {
  max-width: 1000px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 10px;
  /* background-color: rgb(58 120 253 / 35%); */
  background: linear-gradient(120deg, rgba(212, 41, 195, 1) 0%, rgba(255, 67, 49, 1) 50%, rgba(244, 157, 93, 1) 100%);
  padding: 20px;
  border-radius: 10px;
}

.madeMoves {
  margin: 10px 0;
  font-size: 20px;
}

.card{
  max-width: 150px;
  width: 150px;
  height: 150px;
  cursor: pointer;
}
.card.flipped > div{
  transform: rotateY(180deg);
}

.resetButton {
  margin: 20px 0;
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  font-size: 20px;
  background-color: rgb(58 120 253 / 50%);
  color: #eee;
  transition: 0.2s;
}

.resetButton:hover {
  background-color: #875085;
}

.resetButton:active {
  background-color: #734471;
}
